
import { ref, defineComponent } from "vue";

import * as Connection from "../src/connection";

import Page from "./a/page-component.vue";
import Card from "./a/card-component.vue";
import CardHeader from "./a/card-header.vue";
import CardBody from "./a/card-body.vue";
import Row from "./a/row-component.vue";
import RowCell from "./a/row-cell.vue";
import Btn from "./a/btn-component.vue";

export default defineComponent({
  components: {
    Page,
    Card,
    CardHeader,
    CardBody,
    Row,
    RowCell,
    Btn,
  },
  setup() {
    const inPort = ref("");

    const virtualInPorts = ref(
      Connection.loginStatus.userDataConfig.virtualInPorts
    );

    const inPortSplice = (idx: number) => {
      virtualInPorts.value.splice(idx, 1);
    };

    const addInPort = () => {
      if (
        inPort.value &&
        virtualInPorts.value.indexOf(
          inPort.value
        ) === -1
      ) {
        virtualInPorts.value.push(inPort.value);
      }
    };

    return { addInPort, inPortSplice, virtualInPorts, inPort };
  },
});
